.custom-button {
    min-width: 165px;
    width: auto;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    background-color: rgb(190, 17, 17);
    color: white;
    text-transform: uppercase;
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin: auto;
  
    &:hover {
      background-color: rgb(160, 15, 15);
      
    }

    &.google-sign-in {
      background-color: #4285f4;
      color: white;

        &:hover {
          background-color: #357ae8;
          border:none;
        }
    }

    &.inverted{
      background-color: white;
      color: black;
      border: 1px solid black;

      &:hover {
        background-color: black;
        color: white;
        border: none;
      }
    }
  }
  